<template>
  <div>
    <b-row>
      <b-alert v-model="showSuccessAlert" variant="success" dismissible>
        {{ alertMessage }}
      </b-alert>
    </b-row>
    <b-row>
      <nutrition-overview
        :totalCustomers="numberOfCustomers"
        :activeCustomers="activeCustomers"
        @totalCustomersIsActive="setFilterTotalIsActive"
        @activeCustomerIsActive="setFilterActiveIsActive"
      ></nutrition-overview>
    </b-row>
    <b-row class="mt-3">
      <b-card>
        <b-row align-h="between">
          <b-col cols="6">
            <h3>{{ tableHeader }}</h3>
          </b-col>
          <b-col cols="2">
            <b-row>
              <b-col>
                <b-button
                  variant="primary"
                  id="show-btn"
                  @click="showCreateModal"
                >
                  <b-icon-plus class="text-white"></b-icon-plus>
                  <span class="h6 text-white">New Nutrition</span>
                </b-button>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
        <b-row class="mt-3">
          <b-table
            striped
            hover
            :items="items"
            :fields="fields"
            class="text-center"
          >
            <template #cell(contact_name)="data">
              {{
                `${data.item.contact_firstname} ${data.item.contact_lastname}`
              }}
            </template>
            <template #cell(customer_status)="data">
              <b-icon-bookmark-check-fill
                variant="success"
                v-if="data.item.customer_status === 'active'"
              ></b-icon-bookmark-check-fill>
              <b-icon-bookmark-x-fill
                variant="danger"
                v-else
              ></b-icon-bookmark-x-fill>
            </template>
            <template #cell(actions)="data">
              <b-row>
                <b-col cols="7">
                  <b-icon-pencil-square
                    class="action-item"
                    variant="primary"
                    @click="getRowData(data.item.id)"
                  ></b-icon-pencil-square>
                </b-col>
                <b-col cols="1">
                  <b-icon-trash-fill
                    class="action-item"
                    variant="danger"
                    @click="showDeleteModal(data.item.id)"
                  ></b-icon-trash-fill>
                </b-col>
              </b-row>
            </template>
          </b-table>
        </b-row>
      </b-card>
    </b-row>

    <!-- Modal for adding new customers -->
    <b-modal
      ref="create-nutrition-modal"
      size="xl"
      hide-footer
      title="New Nutrition"
    >
      <create-nutrition-form
        @closeCreateModal="closeCreateModal"
        @reloadDataTable="getActivityData"
        @showSuccessAlert="showAlertCreate"
      ></create-nutrition-form>
    </b-modal>

    <!-- Modal for updating customers -->
    <b-modal
      ref="edit-nutrition-modal"
      size="xl"
      hide-footer
      title="Edit Nutrition"
    >
      <edit-nutrition-modal
        @closeEditModal="closeEditModal"
        @reloadDataTable="getNutritionData"
        @showSuccessAlert="showAlertUpdate"
        :customerId="customerId"
      ></edit-nutrition-modal>
    </b-modal>

    <!-- Delete Customer Modal -->
    <b-modal
      ref="delete-nutrition-modal"
      size="md"
      hide-footer
      title="Confirm Deletion"
    >
      <delete-nutrition-modal
        @closeDeleteModal="closeDeleteModal"
        @reloadDataTable="getNutritionData"
        @showDeleteAlert="showDeleteSuccessModal"
        :customerId="customerId"
      ></delete-nutrition-modal>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import NutritionOverview from "@/components/Nutrition/NutritionOverview.vue";
import CreateNutritionForm from "@/components/Nutrition/CreateNutritionForm.vue";
import EditNutritionModal from "@/components/Nutrition/EditNutritionModal.vue";
import DeleteNutritionModal from "@/components/Nutrition/DeleteNutritionModal.vue";

export default {
  components: {
    NutritionOverview,
    CreateNutritionForm,
    EditNutritionModal,
    DeleteNutritionModal,
  },
  data() {
    return {
      // Note 'isActive' is left out and will not appear in the rendered table

      fields: [
        {
          key: "noofcaloriestaken",
          label: "No of Calories taken",
          sortable: false,
        },
        {
          key: "foodname",
          label: "Foodname",
          sortable: false,
        },
        {
          key: "datetime",
          label: "Datetime",
          sortable: false,
        },
        "actions",
      ],
      items: [],
      numberOfCustomers: 0,
      activeCustomers: 0,
      activeCustomersData: [],
      customerId: 0,
      companySearchTerm: "",
      tableHeader: "",
      showSuccessAlert: false,
      alertMessage: "",
    };
  },
  mounted() {
    this.getNutritionData();
  },
  methods: {
    showCreateModal() {
      this.$refs["create-nutrition-modal"].show();
    },
    closeCreateModal() {
      this.$refs["create-nutrition-modal"].hide();
    },
    getNutritionData() {
      let useremail = localStorage.getItem("email");
      axios
        .get(
          "https://health-tracker-production-5285.up.railway.app/api/nutritions/email/" +
            useremail
        )
        .then((response) => {
          this.tableHeader = "Total Customer";
          this.items = response.data;

          let caloriestaken = 0;
          response.data.map((nut) => {
            caloriestaken = caloriestaken + nut.noofcaloriestaken;
          });
          this.numberOfCustomers = caloriestaken;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getRowData(id) {
      this.$refs["edit-nutrition-modal"].show();
      this.customerId = id;
    },
    closeEditModal() {
      this.$refs["edit-nutrition-modal"].hide();
    },
    setFilterTotalIsActive() {
      this.tableHeader = "Total Customers";
      this.getNutritionData();
    },
    setFilterActiveIsActive() {
      this.tableHeader = "Active Customers";
      this.items = this.activeCustomersData;
    },
    showAlertCreate() {
      this.showSuccessAlert = true;
      this.alertMessage = "Nutrition was created successfully!";
    },
    showAlertUpdate() {
      this.showSuccessAlert = true;
      this.alertMessage = "Nutrition was updated successfully";
    },
    showDeleteModal(id) {
      this.$refs["delete-nutrition-modal"].show();
      this.customerId = id;
    },
    closeDeleteModal() {
      this.$refs["delete-nutrition-modal"].hide();
    },
    showDeleteSuccessModal() {
      this.showSuccessAlert = true;
      this.alertMessage = "Nutrition was deleted successfully!";
    },
  },
};
</script>

<style>
.action-item:hover {
  cursor: pointer;
}
</style>
